import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { HttpClient,HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  data = {
    areas: '',
    peoples: '',
    lat: 0,
    long: 0,
    additional_info: ''
  };
  constructor(public navCtrl: NavController, private geolocation: Geolocation, private http: HttpClient) {

  }

  submit() {


    const data = JSON.stringify(this.data);
    const endpoint = `http://www.bytekat.com/yasar/rescue/index.php`;
    const headers = new HttpHeaders({'Content-Type': 'application/x-www-form-urlencoded'});
    this.http.post(endpoint, 'data=' + data, {headers: headers}).subscribe( response => {
      console.log(response);
    });
  }

  getLocations() {
    this.geolocation.getCurrentPosition().then((resp) => {
      this.data.lat = resp.coords.latitude;
      this.data.long = resp.coords.longitude;
     }).catch((error) => {
       console.log('Error getting location', error);
     });

  }

}



